import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import elasticlunr from 'elasticlunr';
import React, { Component } from 'react'; 
import Table from 'react-bootstrap/Table';
import Badge from 'react-bootstrap/Badge';
import euData from './eu';
import cnData from './cn';
import usaData from './usa';
import hkData from './hk';
import Fuse from 'fuse.js';


var usaIndex = elasticlunr(function () {
    this.addField('title');
	this.addField('body');
    this.setRef('id');
});



var index = elasticlunr(function () {
    this.addField('title');
	this.addField('body');
    this.setRef('id');
});


var euList = euData.deviceList;
console.log(euList[2]);
var newEUList = new Array()
for (var i=0; i< euList.length; i++) {
  var commercial_name = euList[i].commercial_name
  var manufacturer = euList[i].manufacturer.name
  var hsc_common_list = euList[i].hsc_common_list
  var doc = {"id": i,
                "title": commercial_name +  " " + manufacturer,
				"body": "",
				"commercial_name": commercial_name, 
				"hsc_common_list": hsc_common_list,
				"manufacturer": manufacturer, 
                "source": "EU",
				"source_label": "" }
  index.addDoc(doc);
  newEUList.push(doc);
}


const options = {
  includeScore: true,
  threshold: 0.2,
  keys: ['title', 'commercial_name', 'manufacturer']
}

const euFuse = new Fuse(newEUList, options)


var usaList = usaData;
var newUSAList = new Array()
for (var i=0; i< usaList.length; i++) {
  var commercial_name = usaList[i].commercial_name
  var manufacturer = usaList[i].manufacturer
  var hsc_common_list = "N/A"
  var doc = {"id": i,
                "title": commercial_name +  " " + manufacturer,
				"body": "",
				"commercial_name": commercial_name, 
				"hsc_common_list": hsc_common_list,
				"manufacturer": manufacturer, 
                "source": "FDA",
				"source_label": "FDA" }
  newUSAList.push(doc)
}


const usaFuse = new Fuse(newUSAList, options)



var hkList = hkData;
var newHKList = new Array()
for (var i=0; i< hkList.length; i++) {
  var commercial_name = hkList[i].commercial_name
  var manufacturer = hkList[i].manufacturer
  var hsc_common_list = "N/A"
  var doc = {"id": i,
                "title": commercial_name +  " " + manufacturer,
				"body": "",
				"commercial_name": commercial_name, 
				"hsc_common_list": hsc_common_list,
				"manufacturer": manufacturer, 
                "source": "HK",
				"source_label": "" }
  newHKList.push(doc)
}


const hkFuse = new Fuse(newHKList, options)


var cnList = cnData;
for (var i=0; i< cnList.length; i++) {
  var commercial_name = cnList[i].commercial_name
  var manufacturer = cnList[i].manufacturer
  var hsc_common_list = "N/A"
  console.log(cnList[i]);
}

class NameForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: 'Abbott', result: []};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({...this.state, value: event.target.value});
  }

  handleSubmit(event) {
    const keyword = this.state.value;
    var euResult = euFuse.search(keyword).map(r => ({ref: r.refIndex, source: "EU"}));
    var usaResult = usaFuse.search(keyword).map(r => ({ref: r.refIndex, source: "FDA"}));
    var hkResult = hkFuse.search(keyword).map(r => ({ref: r.refIndex, source: "HK"}));
    var result = hkResult.concat(usaResult).concat(euResult);
    for (var i=0; i < cnList.length; i++) {
       var obj = cnList[i];
       var body = obj.commercial_name + " " + obj.manufacturer
       if (body.search(keyword) >= 0) {
          result.push({ref: i, source: "CN"})
       }
    }
    
    this.setState({...this.state, result: result});
    event.preventDefault();
  }

  badgeByHsc(doc) {
	var badgeColor = "secondary"
	if (doc.hsc_common_list == "Yes") {
	  badgeColor = "success"
	}
	if (doc.hsc_common_list == "No") {
	  badgeColor = "danger"
	}
	const hsc = (<Badge bg={badgeColor}>{doc.hsc_common_list}</Badge>)
	return hsc
  }

  renderResult(r) {
    if (r.source == "EU") {
      var doc = newEUList[r.ref]
      return (<tr key={"EU" + r.ref}><td>{doc.commercial_name}</td><td>{doc.manufacturer}</td><td>{doc.source_label}</td><td>{this.badgeByHsc(doc)}</td></tr>)
    }
    if (r.source == "HK") {
      var doc = newHKList[r.ref]
      return (<tr key={"HK" + r.ref}><td>{doc.commercial_name}</td><td>{doc.manufacturer}</td><td>{doc.source_label}</td><td>{this.badgeByHsc(doc)}</td></tr>)
    }

    if (r.source == "FDA") {
      var doc = newUSAList[r.ref]
      return (<tr key={"FDA" + r.ref}><td>{doc.commercial_name}</td><td>{doc.manufacturer}</td><td>{doc.source_label}</td><td>{this.badgeByHsc(doc)}</td></tr>)
    }

    if (r.source == "CN") {
      var doc = cnList[r.ref]
      var source_label = "";
      doc.source = "CN"
      doc.hsc_common_list = "N/A"
	  var commercial_name = doc.commercial_name + " " + "(" + doc.register_no + ")"
      return (<tr key={"CN" + r.ref}><td>{commercial_name}</td><td>{doc.manufacturer}</td><td>{source_label}</td><td>{this.badgeByHsc(doc)}</td></tr>)

    }
    return (<tr></tr>);
  }

  banner() {
    return (
      <div class="bg-light p-5">
        <h1></h1>
        <p> 
		  FDA inbox <a href="https://www.facebook.com/howawong.hww" about="_blank">FB</a><br/><br/>
        </p>
      </div>	
	)
  }

  render() {
    return (
      <div>
	    {this.banner()}
      <div className="p-5">
      <form onSubmit={this.handleSubmit}>
          
          <input type="text" value={this.state.value} onChange={this.handleChange} />
		&nbsp;
		&nbsp;
		&nbsp;
        <input type="submit" value="" />
		<br/>
		<br/>
		<br/>
		<Table striped bordered>
		   <tr>
		     <th>
			   
			 </th>
			 <th>
			   
			 </th>
			 <th>
			   
			 </th>
			 <th>
			   HSC Common List ()
			 </th>
		   </tr>
           { this.state.result.map(r => this.renderResult(r)) }
		</Table>
      </form>
        </div>
      </div>
    );
  }
}

export default NameForm;
